import { mapState,mapGetters } from "vuex";
import uOrdinaryList from '../../../components/page-component/u-goods-list/u-ordinary-list.vue';
import appNoGoods from '../../../components/page-component/app-no-goods/app-no-goods.vue';

export default {
    data() {
        return {
            list: [],
            nav_list: [],
            cat_id: -1,
            more: false,
            loading: false,
            first: false,
            page: 1,
            activityBg: '',
            banner: '',
            activeIndex: 0,
            style: -1
        }
    },
    components: {uOrdinaryList,appNoGoods},
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            compositionImg: state => state.mallConfig.__wxapp_img.composition
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
    },
    onReachBottom() {
        if(this.more) {
            this.getMore();
        }
    },
    methods: {
        requestCats(cat_id) {
            this.$request({
                url: this.$api.wholesale.cats
            }).then(res => {
                if (res.code === 0) {
                    this.nav_list = this.nav_list.concat(res.data.list);
                    !cat_id && (this.cat_id = this.nav_list[0].id);
                    this.getList(0);
                }
            });
        },
        changeStatus(id, index) {
            this.cat_id = id;
            this.page = 1;
            if(index < 2) {
                this.activeIndex = 0
            }else {
                this.activeIndex = index - 1
            }
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList(id);
        },
        inputBlur() {
            let that = this;
            setTimeout(v => {
                that.getFocus = false;
                that.getList();
            }, 300)
        },
        tabShow() {
            this.style = this.style == 1 ? 4 : 1
        },
        jump(data) {
            uni.navigateTo({
                url: data.page_url
            });
        },
        getMore() {
            this.more = false;
            this.loading = true;
            uni.showLoading({
                mask: true,
                title: '加载更多...'
            });
            this.$request({
                url: this.$api.wholesale.index,
                data: {
                    cat_id: this.cat_id,
                    page: this.page
                }
            }).then(response=>{
                this.$hideLoading();
                uni.hideLoading();
                if(response.code === 0) {
                    this.list = this.list.concat(response.data.list);
                    this.loading = false;
                    if(response.data.list.length == response.data.pagination.pageSize) {
                        this.more = true;
                        this.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
        getList(id) {
            this.more = false;
            this.loading = true;
            this.list = [];
            this.$request({
                url: this.$api.wholesale.index,
                data: {
                    cat_id: id
                }
            }).then(response=>{
                this.first = false;
                this.$hideLoading();
                uni.hideLoading();
                if(response.code === 0) {
                    this.list = response.data.list;
                    this.banner = response.data.banner;
                    this.loading = false;
                    this.$forceUpdate();
                    if(this.list.length == response.data.pagination.pageSize) {
                        this.more = true;
                        this.page++;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },
    },

    onLoad(option) { this.$commonLoad.onload(option);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.first = true;
        that.requestCats(option.cat_id);
    }
}
